import React from 'react'

import {withStyles} from '@material-ui/core/styles'
import Table from '@material-ui/core/Table';
import TableBody from '@material-ui/core/TableBody';
import TableCell from '@material-ui/core/TableCell';
import TableContainer from '@material-ui/core/TableContainer';
import TableHead from '@material-ui/core/TableHead';
import TableRow from '@material-ui/core/TableRow';
import Paper from '@material-ui/core/Paper';
function createData(name, calories, fat, carbs, protein) {
    return { name, calories, fat, carbs, protein };
  }
  
  const rows = [
    createData('Frozen yoghurt', 159, 6.0, 24, 4.0),
    createData('Ice cream sandwich', 237, 9.0, 37, 4.3),
    createData('Eclair', 262, 16.0, 24, 6.0),
    createData('Cupcake', 305, 3.7, 67, 4.3),
    createData('Gingerbread', 356, 16.0, 49, 3.9),
  ];
class Presentation extends React.Component{

    render(){
        const { classes } = this.props;
        return(
            <>
                <TableContainer component={Paper}>
                    <Table className={classes.table} aria-label="simple table">
                        <TableHead>
                        <TableRow>
                            <TableCell>Sunday [🔴LIVE]</TableCell>
                            <TableCell>Monday</TableCell>
                            <TableCell>Tuesday</TableCell>
                            <TableCell>Wednesday</TableCell>
                            <TableCell>Thursday</TableCell>
                            <TableCell>Friday [🔴LIVE]</TableCell>
                            <TableCell>Saturday [🔴LIVE]</TableCell>
                        </TableRow>
                        </TableHead>
                        <TableBody>
                            <TableRow key="schedule">
                                <TableCell>Open World/Action/Others</TableCell>
                                <TableCell>---</TableCell>
                                <TableCell>---</TableCell>
                                <TableCell>---</TableCell>
                                <TableCell>Grand Theft Auto: 5 *gameplays*</TableCell>
                                <TableCell>FPS/Competitive Games</TableCell>
                                <TableCell>Survival/Others</TableCell>
                            </TableRow>
                            <TableRow key="schedule">
                                <TableCell>GTA Online, Fall Guys, Roblox</TableCell>
                                <TableCell>---</TableCell>
                                <TableCell>---</TableCell>
                                <TableCell>---</TableCell>
                                <TableCell>---</TableCell>
                                <TableCell>Fortnite, CS:GO, Valorant, PUBG Mobile</TableCell>
                                <TableCell>Minecraft</TableCell>
                            </TableRow>
                        </TableBody>
                    </Table>
                </TableContainer>
            </>
        )
    }
}

const styles = theme => ({
    root : {
        // padding:'5% 2%'
    },
    table: {
        width: "100%"
    },
    paper : {
        padding: theme.spacing(2),
        textAlign: 'center'
    }
});

export default withStyles(styles)(Presentation);